<template>
  <div class="container">
    <div class="card card-primary">
      <div class="card-header">
        <h3 class="card-title">Добавити фільм</h3>
      </div>
      <form>
        <div class="card-body">
          <div class="form-group">
            <label for="nameFilm">Назва фільма</label>
            <input
              type="text"
              class="form-control"
              id="nameFilm"
              placeholder="Введіть назву фільма"
              v-model="nameFilm"
            />
          </div>
          <div class="form-group">
            <label for="description">Опис</label>
            <textarea
              type="text"
              class="form-control"
              id="description"
              placeholder="Текст"
            ></textarea>
          </div>
          <div>
              <label for="holovnaImg">Головна картинка</label>
              <input type="file" class="custom-file-input" @change="uploudFile">
              <img :src="imageUrl">
          </div>
        </div>  
        <div class="card-footer">
          <button type="submit" class="btn btn-primary">Submit</button>
        </div>
      </form>
    </div>
  </div>
</template>
<script>

//import { ref } from 'vue';
//import { storage } from '@/firebase/index.js'
import { getStorage, ref, uploadBytes, put } from 'firebase/storage'

export default {
  setup() {
    const imageUrl = ref(null)

    const uploudFile = async (event) => {
      const file = event.target.files[0]
      const storage = getStorage()
      const storageRef = ref(storage, 'images/' + file.name)
      //storageRef.put(file)
      await uploadBytes(storageRef, file);
      console.log(event.target.files[0])
      // const fileRef = storageRef.child(file.name)
      // await uploadBytes(fileRef, file)
      //const url = await fileRef.getDownloadURL()
      // photoUrl.value = url
    }

    return { imageUrl, uploudFile }
  },
}
</script>
<style lang="scss">

.container{
    width: 70%;
}
input[type="file"] {
  display: block;
  opacity: 1;  
}

.upload-button {
  display: inline-block;
  border: 1px solid #ccc;
  padding: 6px 12px;
  cursor: pointer;
}

.upload-preview {
  display: inline-block;
  width: 100px;
  height: 100px;
  border: 1px solid #ccc;
  margin-left: 20px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
label{
  display: block;
  text-align: left;
}
</style>
